.app-heading {
  text-align: center; }
  .app-heading__title {
    font-size: 50px;
    line-height: 70px;
    font-weight: 800;
    text-transform: uppercase;
    margin: 0; }
  .app-heading__subtitle {
    font-size: 25px;
    line-height: 40px;
    font-weight: 300;
    margin: 0;
    margin-top: 10px; }
  .app-heading.secondary {
    padding-top: 80px;
    padding-bottom: 50px; }
    .app-heading.secondary .app-heading__title {
      position: relative;
      font-size: 30px;
      line-height: 60px;
      font-weight: 400; }
      .app-heading.secondary .app-heading__title::before, .app-heading.secondary .app-heading__title::after {
        content: '';
        position: absolute;
        left: 50%;
        transform: translateX(-50%); }
      .app-heading.secondary .app-heading__title::before {
        width: 130px;
        border: 3px solid #d6d6d6;
        top: -10px;
        bottom: -10px;
        z-index: -2; }
      .app-heading.secondary .app-heading__title::after {
        width: 100%;
        height: 100%;
        z-index: -1;
        background-color: #ffffff; }
    .app-heading.secondary .app-heading__subtitle {
      font-size: 16px;
      line-height: 28px;
      font-weight: 300;
      margin-top: 60px; }
