html,
body {
  margin: 0;
  font-family: 'Montserrat';
  color: #36363b; }

.bg-cover {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.app-btn {
  font-size: 16px;
  line-height: 30px;
  font-weight: 400;
  text-transform: uppercase;
  text-decoration: none !important;
  outline: none !important;
  display: inline-flex;
  justify-content: center;
  align-items: center; }
  .app-btn:hover, .app-btn:active, .app-btn:visited, .app-btn:focus {
    text-decoration: none !important;
    color: #36363b !important; }
  .app-btn--white {
    background-color: #ffffff;
    border: 2px solid #ffffff;
    color: #36363b; }
    .app-btn--white:hover {
      background-color: #36363b;
      color: #ffffff !important; }
  .app-btn--black {
    background-color: #36363b;
    border: 2px solid #36363b;
    color: #ffffff; }
    .app-btn--black:hover {
      background-color: #ffffff; }
  .app-btn--outlined {
    border: 2px solid #36363b;
    color: #36363b;
    background-color: transparent; }
  .app-btn--sm {
    padding: 5px 10px;
    min-width: 150px; }
  .app-btn--md {
    padding: 13px 10px;
    min-width: 250px; }
